<template>

    <div class='container-fluid main-content content' >
        <div class='row' style="height:150px;background-color:#3F729B;position:relative;bottom:25px;">
            <span style="position:relative;top:50px;">
                <h3 class="center-text white-text secondary-text">Blog</h3>
            </h5>
        </div>
        <div class='row' style="position:relative;bottom:75px;">
            <div class='col-sm-12 col-md-12 col-lg-2'>
                
            </div>
            
            <div class='col-sm-12 col-md-12 col-lg-8' v-if='!dataLoading'>
                <div v-for='post in sortByDate'>
                    <preview :name='post.name' :description='post.description' :body='post.body'
                    :date='post.date' :category='post.category' :id='post._id' :comments='post.comments' :content='post.content'></preview>
                </div>
            </div>
            
            <div class='col-md-12 secondary-text' v-if="dataLoading">
                <i class="material-icons center-text" style="font-size:96px;position:fixed;top:40%;opacity:0.50">insert_drive_file</i>
            </div>
            
        </div>    
    </div>
    
</template>



<script>

import Preview from '../components/reusable/Preview'

export default {
  name: 'blog',
  components: {
      Preview
  },
    data () {
        return{
          title: 'Technology in 2017',
          posts: [],
          dataLoading: false
        }
    },
    methods: {
        getBlogPosts: function (){
            preloader.on();
            this.dataLoading = true;
            axios.get('https://trash-server.herokuapp.com/blog_api/api/posts')
                .then(response => this.posts = response.data)
                .then(_ =>{                
                    this.dataLoading = false;
                    preloader.off();
                })
        }
    },
    computed:{
        sortByDate: function(){
            this.posts.sort( (a,b) => {
                return new Date(b.date) - new Date(a.date);
            });
            return this.posts;
        }
    },
    created() {
        this.getBlogPosts()
    }
 
}
</script>



<style scoped>

.avi{
    margin: 0 auto;
}

</style>


