<template>
    <div class="container-fluid main-content content">
        <div class="col-md-3 col-lg-3" style="padding:15px;">
            
        </div>
        
        <div class="col-md-9 col-lg-6" v-if='!dataLoading && reviews.length > 0'>
        
            <div v-for='post in reviewsByDate'>
                <div class="music-post card" style="padding:10px 0">
                    <div>
                        <div style="float:left;position:relative;left:15px;" class="tertiary-color">{{post.category}}</div>
                        <div style="float:right;position:relative;right:15px;color:#F9A825">
                            <i class="material-icons" v-if="post.rating > 0">star</i>
                            <i class="material-icons" v-if="post.rating > 1">star</i>
                            <i class="material-icons" v-if="post.rating > 2">star</i>
                            <i class="material-icons" v-if="post.rating > 3">star</i>
                            <i class="material-icons" v-if="post.rating > 4">star</i>
                        </div>
                    </div>
                        <div class="card-block" style="padding-top:35px;">
                            <span>
                                <h3 >{{post.name}}</h3>
                                <p class="card-text secondary-text">{{post.content}}</p>
                            </span>
                        </div>
                        <!-- Card footer -->
                        <div class="card-data" style="padding-bottom:0;">
                            <ul>
                                <li><i class="fa fa-clock-o"></i> {{post.date | moment}} <span style="margin-left:20px;"><i class="material-icons" style="position:relative;top:3px;font-size:18px;">comment</i> 5</span></li>
                            </ul>
                        </div>
                        
                </div>
            </div>
             
        
        
            <div class="col-md-12 col-lg-3" style="padding:15px;">
                <h3 class="music-headers">Songs of the Year</h3>
                <iframe src="https://embed.spotify.com/?uri=spotify%3Auser%3A1226191915%3Aplaylist%3A78xydTNUOusjhekwXivJdU&theme=white" width="100%" height="380" frameborder="0" allowtransparency="true"></iframe>
            </div>

        </div>
        
        <div class='col-md-12 secondary-text' v-if="dataLoading">
            <i class="material-icons center-text" style="font-size:96px;position:fixed;top:40%;opacity:0.50">library_music</i>
        </div>
        
        <div class='col-md-12 secondary-text' v-if="reviews.length == 0 && !dataLoading">
            <h4 class="center-text" style="position:relative;top:90px;opacity:0.50">No Reviews Yet</h4>
        </div>
        
    </div>
</template>

<script>
import moment from 'moment'

    export default {
        name: 'review',
        filters: {
            moment: function (date) {
                return moment(date).format('MMMM Do YYYY, h:mm:ss a');
            }
        },
        data () {
            return{
            reviews: [],
            dataLoading: false
            }
        },
        methods: {
            getReviews: function (){
                preloader.on();
                this.dataLoading = true;
                axios.get('https://trash-server.herokuapp.com/blog_api/api/review')
                    .then(response => this.reviews = response.data)
                    .then(_ =>{                
                        this.dataLoading = false;
                        preloader.off();
                    })
                
            }
        },
        computed:{
            reviewsByDate: function(){
                this.reviews.sort( (a,b) => {
                    return new Date(b.date) - new Date(a.date);
                });
                return this.reviews;
            }
        },
        created() {
            this.getReviews()
        }
}
</script>

<style>
    .music-post{
        padding:10px;
    }
    .album-art{
        height:50%;
        width:50%;
    }
    .container-aotw{
        padding-bottom:15px;
    }
    .artist-aotw{
        
    }
    .album-aotw{
        font-weight:bold;
    }
    .date-aotw{
        font-weight:bold;
    }
    .music-headers{
        padding-bottom:5px;
    }
</style>


