<template>

    <div class='container-fluid main-content content'>
        
        <div class="row">
            <div class="col-md-4">
                <h3>Blog</h3>
                <p>Rant about dumb shit, talk about life, do whatever you want. At the end of the day nobody will care!</p>
            </div>
            <div class="col-md-4">
                <h3>Reviews</h3>
                <p>Review everything from music to memes.</p>
            </div>
            <div class="col-md-4">
                <h3>Meme Feed (COMING SOON)</h3>
                <p>This is what the world needs. Stay up to date with the worlds latest memes, it's a war out there.</p>
            </div>
        </div>
        
    </div>
    
</template>

<script>

import Preview from '../components/reusable/Preview'

export default {
  name: 'home',
  components: {
      Preview
  },
    data () {
        return{
          title: 'Technology in 2017',
          posts: [],
          dataLoading: false
        }
    },
    methods: {
        getBlogPosts: function (){
            preloader.on();
            this.dataLoading = true;
            axios.get('https://trash-server.herokuapp.com/blog_api/api/posts')
                .then(response => this.posts = response.data)
                .then(_ =>{                
                    this.dataLoading = false;
                    preloader.off();
                })
        }
    },
    created() {
        this.getBlogPosts()
    }
 
}
</script>

<style scoped>

.avi{
    margin: 0 auto;
}
body{
    background-color:#FFFFFF !important;
}

</style>




